
.project-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* This ensures 3 cards per row */
  gap: 20px;
  padding: 40px;
  margin: 20px;
  max-height: 800px; /* Adjust the height as needed */
  overflow-y: auto;
}

.project-card {
  height: 250px;
  perspective: 1000px;
}

.project-card:hover .project-content {
  transform: rotateY(15deg) rotateX(10deg);
}

.project-content {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #242424, #84c5cb);
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transform: rotateY(0) rotateX(0);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  font-family: Arial, sans-serif;
}

.project-content h3 {
  margin: 0 0 10px;
  font-size: 1.5em;
}

.project-content p {
  font-size: 1em;
  margin: 0 0 20px;
}

.project-content a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  background: rgba(255, 255, 255, 0.2);
  padding: 10px 15px;
  border-radius: 5px;
  transition: background 0.3s ease;
}

.project-content a:hover {
  background: rgba(255, 255, 255, 0.4);
}

.load-more-btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #346b70;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
  /* margin-top: 20px; Add some space from the project cards */
}

.load-more-btn:hover {
  background-color: #659a9f;
}

.center-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1200px) {
  .project-container {
    grid-template-columns: repeat(2, 1fr); /* 2 columns for medium screens */
  }
}

@media (max-width: 768px) {
  .project-container {
    grid-template-columns: 1fr; /* 1 column for small screens */
    padding: 20px;
    margin: 10px;
  }

  .project-content h3 {
    font-size: 1.2em; /* Adjust font size for smaller screens */
  }

  .project-content p {
    font-size: 0.9em;
  }
}

@media (max-width: 480px) {
  .project-container {
    grid-template-columns: 1fr; /* 1 column for very small screens (e.g., mobile phones) */
    padding: 15px;
    margin: 5px;
  }

}

