/* General styles */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
  background-color: #000000;
  background-image: url(../public/background_image.jpg); /* Image from public folder */
  background-size: cover;
  background-position: center;
}

.app-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
}

/* Sidebar styles */
.sidebar {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 40px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.sidebar .icon {
  display: block;
  font-size: 30px;
  color: rgb(0, 0, 0);
  margin: 15px 0;
  text-align: center;
  text-decoration: none;
  transition: color 0.3s ease;
}

.icon:hover {
  color: #fea918; /* Hover effect */
}

/* Content styles */
.content {
  margin-left: 80px;
  padding: 20px;
  flex: 1;
  color: white;
}


.App .social-media-icons {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.App .icon {
  color: #ffffff;
  font-size: 40px;
  transition: transform 0.3s ease, color 0.3s ease;
}

.App .icon:hover {
  transform: scale(1.2);
  color: #fea918; /* Highlight color for hover */
}

.App a {
  text-decoration: none;
}

.circle {
  height: 35px;
  width: 35px;
  border-radius: 24px;
  background-color: rgb(0, 0, 0);
  position: fixed; 
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 99999999; /* so that it stays on top of all other elements */
}

.wave-text {
  font-size: 15rem;
  font-family: 'Indie Flower';
  animation: sineWave 3s ease-in-out infinite;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wave-fname {
  font-size: 6rem;
  font-family: 'Indie Flower';
  color: #fea918;
  animation: sineWave 3s ease-in-out infinite;
  display: flex;
  justify-content: center;
  align-items: center;
}

.description {
  font-size: 2rem;
  font-family: 'Indie Flower';
  animation: sineWave 3s ease-in-out infinite;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
