.wave-container .home-showProjects {
  display: flex;
  justify-content: center;
}

.showProjects-btn {
  display: inline-block;
  padding: 10px 20px;
  font-family: 'Indie Flower';
  background-color: #fea918;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}

.showProjects-btn:hover {
  background-color: #84c5cb;
}

.bou {
  color: #fea918;
}

@keyframes sineWave {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-20px);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}

/* Media Queries for Responsiveness */

/* For Tablets and smaller screens */
@media (max-width: 1800px) {
  .wave-text {
    font-size: 100px; /* Adjust font size for smaller screens */
  }

  .wave-fname {
    font-size: 100px; /* Adjust font size for smaller screens */
  }

  .description {
    font-size: 1.5rem; /* Adjust font size for smaller screens */
  }

  .showProjects-btn {
    font-size: 16px; /* Adjust font size for smaller screens */
    padding: 8px 16px; /* Adjust padding */
  }
}

@media (max-width: 1400px) {
  .wave-text {
    font-size: 70px; /* Adjust font size for smaller screens */
  }

  .wave-fname {
    font-size: 70px; /* Adjust font size for smaller screens */
  }

  .description {
    font-size: 1.5rem; /* Adjust font size for smaller screens */
  }

  .showProjects-btn {
    font-size: 16px; /* Adjust font size for smaller screens */
    padding: 8px 16px; /* Adjust padding */
  }
}

/* For Tablets and smaller screens */
@media (max-width: 768px) {
  .wave-text {
    font-size: 35px; /* Adjust font size for smaller screens */
  }

  .wave-fname {
    font-size: 35px; /* Adjust font size for smaller screens */
  }

  .description {
    font-size: 1.5rem; /* Adjust font size for smaller screens */
  }

  .showProjects-btn {
    font-size: 16px; /* Adjust font size for smaller screens */
    padding: 8px 16px; /* Adjust padding */
  }
}

/* For Mobile Devices */
@media (max-width: 480px) {
  .wave-text {
    font-size: 35px; /* Smaller font size on mobile */
  }

  .wave-fname {
    font-size: 35px; /* Smaller font size on mobile */
  }

  .description {
    font-size: 1.2rem; /* Smaller font size on mobile */
  }

  .showProjects-btn {
    font-size: 14px; /* Smaller font size on mobile */
    padding: 6px 12px; /* Adjust padding for mobile */
  }
}
